 externo, para seguir siendo competitivas. Para tener éxito en la transformación digital las empresas deben conocer que capacidades poseen, dónde se encuentran y a dónde quieren llegar por lo que deben evaluar y medir su grado de madurez digital. Para ello, se utiliza el índice de madurez digital (IMD). ")]),_c('p',{staticClass:"ml-8 mr-8 mt-4 text-justify text-xl font-bold"},[_vm._v(" El IMD es un instrumento útil para comprender el avance del proceso de transformación digital a través de la evaluación de diferentes ámbitos o dimensiones acorde con el contexto digital, tal como el nivel de adopción de nuevas tecnologías digitales, la disposición de estas dentro de una empresa y la capacidad de utilizar tecnología nueva y existente para beneficio de la empresa y el consumidor. ")]),_c('p',{staticClass:"ml-8 mt-6 font-semibold text-3xl"},[_vm._v(" Ventajas de determinar el IMD ")]),_c('p',{staticClass:"ml-8 mr-8 mt-4 text-justify text-xl"},[_vm._v(" Los resultados obtenidos del modelo constituyen la base para los análisis de objetivos y las mejoras de los procesos, así como para el proceso de transformación digital en general. Desde la perspectiva interna de una empresa, el uso de un modelo de madurez digital aporta los siguientes beneficios: ")]),_c('li',{staticClass:"ml-8 mr-8 mt-4 text-justify text-xl"},[_vm._v(" Reconocimiento de los puntos fuertes y débiles a partir de un marco definido ")]),_c('li',{staticClass:"ml-8 mr-8 mt-4 text-justify text-xl"},[_vm._v(" Definición de medidas para la mejora continua ")]),_c('li',{staticClass:"ml-8 mr-8 mt-4 text-justify text-xl"},[_vm._v(" Orientación a los puntos de referencia ")]),_c('li',{staticClass:"ml-8 mr-8 mt-4 text-justify text-xl"},[_vm._v(" Documentación de la calidad de la empresa ")]),_c('p',{staticClass:"ml-8 mr-8 mt-4 text-justify text-xl"},[_vm._v(" Los resultados obtenidos del modelo constituyen la base para los análisis de objetivos y las mejoras de los procesos, así como para el proceso de transformación