a muchas organizaciones, dado el impacto que las tecnologías digitales generan en el ambiente de las organizaciones. Compañías típicamente necesitan una estrategia digital para ayudar a los gerentes navegar a través de los procesos de transformación. Una estrategia de negocios digitales puede ser vista como una estrategia organizacional formulada y ejecutada al apalancarse con los recursos digitales para crear un valor agregado. ")]),_c('li',{staticClass:"ml-8 mr-8 mt-4 text-justify text-xl"},[_vm._v(" Tecnología ")]),_c('br'),_c('p',{staticClass:"ml-8 mr-8 text-justify text-xl"},[_vm._v(" La capacidad tecnológica transforma como las compañías compiten entre sí, puesto que van incrementando su competitividad no solamente con compañías rivales de la industria sino también con negocios de otras industrias que atraen clientes por medio de ofertas digitales. El uso de la tecnología direcciona la actitud de las compañías respecto a nuevas tecnologías y su habilidad de explotarlas a través de productos que generen impacto, procesos de negocio, canales de venta, logística, etc. ")]),_c('p',{staticClass:"ml-8 mr-8 text-justify text-xl"},[_vm._v(" Nuevas tecnologías digitales como sociales, móviles, de análisis, servicios en la nube, internet de las cosas, entre otras, presentan oportunidades, pero también generan amenazas para compañías ya establecidas. Por lo tanto, la tecnología emerge como un conjunto de capacidades fundamentales que permiten habilitar, desplegar e integrar como soluciones efectivas capaces de apoyar los negocios digitales. ")]),_c('li',{staticClass:"ml-8 mr-8 mt-4 text-justify text-xl"},[_vm._v(" Procesos ")]),_c('br'),_c('p',{staticClass:"ml-8 mr-8 text-justify text-xl"},[_vm._v(" Puesto que las tecnologías digitales son tecnologías interactivas que establecen conexiones con otras organizaciones también permiten a las compañías mejorar su capacidad de innovación al integrar socios o competidores en sus procesos creativos y/o experimentales. Es necesari